import getters from './getters.js'
const state = {
    onOff: true,
    loading: false
};
const mutations = {
    hide (state) {
        state.onOff = false;
    },
    show (state) {
        state.onOff = true;
    },
    sLoading(state) {
        state.loading = true;
    },
    hLoading(state) {
        state.loading = false;
    }
}
export default {
    state,
    mutations,
    getters
}


