<template>
    <div>
        <div class="content">
            <bannerView></bannerView>
            <div class="newsList">
              <ul>
                <li v-for="item in list">
                <router-link :to="'/article/'+item.id">
                  <h2>{{item.title}}</h2>
                  <p>{{item.detail}}</p>
                </router-link>
                </li>
              </ul>
            </div>
      </div>
    </div>
</template>
<script>
    import bannerView from './Banner.vue'
    export default {
      data () {
        return {
          list: []
        }
      },
      mounted () {
        this.$http.get('news-demo/static/data/index.data').then(function (res) {
          this.list = res.data;
        }.bind(this)).catch(function (err){
          alert(err);
        });
      },
      components: {
        bannerView
      }
    }
</script>


