<template>
  <div id="app">
    <loading v-show="loading"></loading>
    <navView v-show="show"></navView>
      <router-view></router-view>
    <footView></footView>
  </div>
</template>

<script>
  import navView from './components/Nav.vue'
  import footView from './components/Foot.vue'
  import {mapActions,mapGetters} from 'vuex'
  export default {
    computed: mapGetters([
      'show',
      'loading'
      ]),
    watch: {
      $route (to,from) {
        if(to.path == '/user-info' || to.path == '/user-login' || to.path == '/user-reg') {
          this.$store.dispatch('hideHeader');
        } else {
          this.$store.dispatch('showHeader');
        }
      }
    },
    data() {
      return {
        food: '222'
      }
    },
    components: {
      navView,
      footView
    }
  }
</script>

<style>
  @import './assets/css/index.css';
</style>



