<template>
    <div>
        <div class="content mt">
            <div class="newsList">
              <ul>
                <li v-for="item in list">
                  <a href=""></a>
                  <h2>{{item.title}}</h2>
                  <p>{{item.detail}}</p>
                </li>            
              </ul>
            </div>
      </div>
    </div>
</template>
<style scoped>
  .mt {
    margin-top : 30px;
  }
</style>
<script>
  export default {
    data () {
      return {
        list: []
      }
    },
    methods: {
      getData () {
        this.$http.get('news-demo/static/data/follow.data').then(function (res){
          this.list = res.data;
        }.bind(this)).catch(function (err){
          alert(err);
        });
      }
    },
    mounted () {
      this.getData();
    }
  }
</script>



