<template>
    <div>
        <div class="content mt">
            <div class="newsList">
              <ul>
                <li v-for="(item, index) in list" :key="index">
                  <a href=""></a>
                  <h2>{{item.title}}</h2>
                  <p v-html="item.content"></p>
                </li>      
              </ul>
            </div>
      </div>
    </div>
</template>
<style scoped>
  .mt {
    margin-top: 30px;
  }
</style>
<script>
  export default {
    data () {
      return {
        list: []
      }
    },
    methods: {
      getData () {
        this.$http.get('news-demo/static/data/article.data').then(function (res){
          this.list = res.data;
        }.bind(this)).catch(function (err){
          console.log(err);
        });
      }
    },
    mounted () {
      this.getData();
    },
    props: ['a','b']
  }
</script>


