<template>
    <div>
        <div class="banner">
            <ul class="clearfix">
              <li >
                <img src="news-demo/static/images/img_1.jpg" alt="">
                <div class="text-box">
                  <h2>BANNER-ONE</h2>
                  <p>11111111</p>
                </div>
              </li>
              <li >
                <img src="news-demo/static/images/img_2.jpg" alt="">
                <div class="text-box">
                  <h2>BANNER-TWO</h2>
                  <p>222222222</p>
                </div>
              </li>
              <li >
                <img src="news-demo/static/images/img_3.jpg" alt="">
                <div class="text-box">
                  <h2>BANNER-THREE</h2>
                  <p>333333</p>
                </div>
              </li>
              <li >
                <img src="news-demo/static/images/img_4.png" alt="">
                <div class="text-box">
                  <h2>BANNER-FOUR</h2>
                  <p>444444444</p>
                </div>
              </li>
            </ul>
            <ol>
              <li class="active"></li>
              <li></li>
              <li></li>
              <li></li>
            </ol>
            </div>
    </div>
</template>
<script>
    export default {
        mounted () {
            this.slider();
        },
        methods: {
            slider: () => {
                $(function (){
                  var mySwipe = Swipe($('.banner')[0],{
                    auto: 1000,
                    continuous: true,
                    stopPropagation: true,
                    callback: (index) => {
                      $('.banner ol li').removeClass('active');
                      $('.banner ol li').eq(index).addClass('active');
                    }
                  });
                });
      }
        }
    };
</script>


